:root {
  --first-main-color: #621304;
  --second-main-color: #b65543;

  --light-text: #ffffff;
  --light-shadow: #ececec;
  --dark-shadow: #5c5c5c;

  --title-font: 'Cinzel', serif;
  --article-font: 'Crimson Pro', serif;
  --multipurpose-font: 'Nanum Gothic', sans-serif;
}

.week-events-container {
  background-position: center;
  background-size: cover;
  height: 100vh;
  width: 100vw;

  display: flex;
}

.week-event-info {
  color: var(--light-text);
  text-transform: uppercase;
  text-shadow: 1px 1px 1px var(--dark-shadow);
  height: 90vh;
  width: 20rem;
  margin: auto;

  align-self: center;
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
}

.all-events-btn {
  background-color: var(--first-main-color);
  color: var(--light-shadow);
  font-family: var(--multipurpose-font);
  text-transform: uppercase;
  text-align: center;
  border: 0;
  border-radius: 0.3rem;
  width: 8rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-bottom: auto;

  align-self: center;
}

.all-events-btn:hover {
  cursor: pointer;
}

.week-event-title {
  font-family: var(--title-font);
  font-size: 2.5rem;
  margin: 0;

  align-self: center;
}

.week-event-type {
  font-family: var(--article-font);
  font-size: 1.5rem;
  margin: 0;
  margin-left: 3rem;
}

.week-event-author {
  font-family: var(--article-font);
  margin: 0;
  margin-left: 3rem;
}

.week-event-dates {
  font-family: var(--article-font);
  margin: 0;
  margin-top: 5rem;
  margin-left: 3rem;
  width: 6rem;
}

.week-tickets-btn {
  background-color: var(--first-main-color);
  color: var(--light-shadow);
  font-family: var(--multipurpose-font);
  text-transform: uppercase;
  text-align: center;
  border: 0;
  border-radius: 0.3rem;
  margin-top: 1rem;
  width: 8rem;
  padding-top: 1rem;
  padding-bottom: 1rem;

  align-self: center;
}

.week-tickets-btn:hover {
  cursor: pointer;
}

/* Slider */
.slider-btn {
  position: absolute;
  top: 150%;
  z-index: 10;

  background-color: var(--first-main-color);
  color: var(--light-text);
  font-family: inherit;
  font-size: 1rem;
  border: none;
  border-radius: 50%;
  height: 1.5rem;
  width: 1.5rem;
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;
}

.slider-btn-left {
  left: 6%;
  transform: translate(-50%, -50%);
}

.slider-btn-right {
  right: 6%;
  transform: translate(50%, -50%);
}

@media (min-width: 650px) {
  .week-event-info {
    width: 40rem;
  }

  .all-events-btn {
    font-size: 1.2rem;
  }

  .week-event-type {
    font-size: 2rem;
    margin-left: 6rem;
  }

  .week-event-title {
    font-size: 3.5rem;
  }

  .week-event-author {
    font-size: 1.7rem;
    margin-left: 6rem;
  }

  .week-event-dates {
    font-size: 1.7rem;
    margin-left: 6rem;
    width: 9rem;
  }

  .week-tickets-btn {
    font-size: 1.2rem;
  }

  .slider-btn {
    font-size: 2rem;
    height: 3rem;
    width: 3rem;
  }
}
